import React from 'react';
import Button from '@mui/material/Button';
import { connect } from 'react-redux';
import { createAccount } from '../actions/AccountActions';

class SignUpForm extends React.Component {
    state = {
        error: ``,
    }

    signUp = (e) => {
        const elements = Array.from(e.target.parentNode.children);
        const [email, fname, lname, password, confirmPassword] = Array.from(e.target.parentNode.children).filter(child => child.value).map(child => child.value);
        if(!fname || !lname || !email || !password || !confirmPassword) {
            this.setState(() => ({ error: 'Please fill out all field before submitting.'}))
        }
        else if(password !== confirmPassword) {
            this.setState(() => ({error: 'Your passwords do not match. Please re-enter them and try again.'}))
        }
        else {
            this.props.dispatch(createAccount({
                fname,
                lname,
                email,
                password,
                confirmPassword,
            }))
        }
    }

    render() {
        return (
            <div className='container'>
                <div className='sign-up-form'>
                    <label htmlFor='input'>Email</label>
                    <input id='email' type='email' name='email'></input>
                    <label htmlFor='input'>First Name</label>
                    <input id='first-name' type='text' name='first-name'></input>
                    <label htmlFor='input'>Last Name</label>
                    <input id='last-name' type='text' name='last-name'></input>
                    <label htmlFor='password'>Password</label>
                    <input id='password' type='password' name='password'></input>
                    <label htmlFor='password'>Confirm Password</label>
                    <input id='con-password' type='password' name='con-password'></input>
                    <Button 
                        variant='outlined' 
                        color='primary' 
                        onClick={this.signUp}
                    >Sign Up</Button>
                    { this.state.error && <p>{this.state.error}</p> }
                </div>
            </div>
        )
    }
}

export default connect()(SignUpForm)