import React from 'react';
import { BrowserRouter, Redirect, Route, Switch } from 'react-router-dom';
import Nav from '../components/Nav';
import LoginForm from '../components/LoginForm';
import SignUp from '../components/SignUpForm';
import DatePickerForm from '../components/DatePickerForm';
import LogRow from '../components/LogRow'
import LogCell from '../components/LogCell';
import Dashboard from '../components/Dashboard';
import '../styles/styles.scss';
import { connect } from 'react-redux';

const Router = (props) => (
    <BrowserRouter>
        <Nav></Nav>
        <Switch>
            <Route exact path='/'>
                { props.loggedIn ? <Redirect to='/dashboard' /> : <LoginForm />}
            </Route>
            <Route path='/login' component={LoginForm} />
            <Route path='/signup' component={SignUp} />
            <Route path='/create-new-log' render={(routeProps) => props.loggedIn ?  <DatePickerForm {...routeProps}/> : <Redirect to='/login' />} />
            <Route path='/logs' render={(routeProps) => props.loggedIn ? <DatePickerForm  {...routeProps}/> : <Redirect to='login' />}></Route>
            <Route path='/log' component={LogRow} />
            <Route path='/dashboard'>
                { props.loggedIn ? <Dashboard /> : <Redirect to='/login' />}
            </Route>
        </Switch>
    </BrowserRouter>
)

const mapStateToProps = (state) => {
    return {
        loggedIn: state.auth.loggedIn,
        error: state.auth.error,
        logData: state.logData,
    }
}

export default connect(mapStateToProps)(Router);