import React from 'react';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import { Carousel } from 'react-responsive-carousel';
import { connect } from 'react-redux';
import LogCell from './LogCell';
import { createNewLog } from '../actions/LogData'

const LogRow = (props) => {
    const peerLogs = [];

    const getPeerLogs = () => {
        let currentDate = props.logData.startDate;
        console.log(currentDate, props.logData.endDate);
        while(currentDate <= props.logData.endDate) {
            const peerLog = props.logData.peerLogs.filter(peerLog => peerLog.startDate === currentDate)[0];
            if(peerLog) {
                peerLogs.push(peerLog)
            }
            else {
                props.dispatch(createNewLog({
                    logType: 'peer',
                    date: currentDate,
                }));
            }
            currentDate = new Date(new Date(currentDate).getTime() + (24 * 60 * 60 * 1000));
        }
        console.log(peerLogs);
        return peerLogs.map(log => <LogCell {...log} />);
    }

    return (
        <div>
            <Carousel emulateTouch={true}>
                <div>
                    <div className='log-row'>
                        
                    </div>
                </div>
                <div>
                    <div className='log-row'>
                        { getPeerLogs().slice(4, 7) }    
                    </div>
                </div>
            </Carousel>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        logData: state.logData,
    }
}

export default connect(mapStateToProps)(LogRow);