import React from 'react';
import { DateRangePicker } from 'react-date-range';
import { connect } from 'react-redux';
import { setAuthError } from '../actions/AuthActions';
import { setDateRange } from '../actions/LogData';

class DatePickerForm extends React.Component {
    state = {
        selectionRange: {
            startDate: new Date(),
            endDate: new Date(),
            key: 'selection'
        },
    }

    showSelect = (ranges) => {
        this.setState(() => ({
            selectionRange: {
                startDate: ranges.selection.startDate,
                endDate: ranges.selection.endDate,
                key: ranges.selection.key
            }
        }))
    }

    createNewLog = () => {
        if(this.validDateRange()) {
            console.log(this.state.selectionRange);
            this.props.dispatch(setDateRange({ startDate: this.state.selectionRange.startDate, endDate: this.state.selectionRange.endDate }));
            this.props.history.push('/log');
        }
        else {
            this.props.dispatch(setAuthError({ error: 'Invalid date range selection. Please select a single week starting on a Sunday and ending on a Saturday.'}));
        }
    }

    validDateRange = () => {
        return this.state.selectionRange.startDate.getDay() === 0 && this.state.selectionRange.endDate.getDay() === 6;
    }

    getStringDates = (dateObj) => {
        const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
        const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
        const dates = this.state.homeworkLogCells.map((val) => {
            const currentDay = new Date(dateObj.selectionRange.startDate.getTime() + (val * 24 * 60 * 60 * 1000));
            return `${days[currentDay.getDay()]} - ${months[currentDay.getMonth()]} ${currentDay.getDate()}, ${currentDay.getFullYear()}`;
        });
        return dates;
    }

    render() {
        return (
            <div className='date-picker-form'> 
                <DateRangePicker 
                    ranges={[this.state.selectionRange]}
                    onChange={this.showSelect}
                />
                <button onClick={this.createNewLog} className='date-picker-form__submit'>Submit</button>
                { this.props.error && <p>{ this.props.error }</p> }
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    error: state.auth.error
})

export default connect(mapStateToProps)(DatePickerForm);