import React from 'react';
import Button from '@mui/material/Button';
import { Link, useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import { setLoggedIn, setAuthError } from '../actions/AuthActions';

const LoginForm = (props) => {
    const history = useHistory();

    const login = (e) => {
        console.log(e);
        const email = document.getElementById('email').value;
        const password = document.getElementById('password').value;
        const user = props.accounts.filter((account) => account.email === email && account.password === password);

        if(user.length > 0) {
            props.dispatch(setLoggedIn({ loggedIn: true }));
            props.dispatch(setAuthError({ error: '' }));
            history.push('/dashboard');
        }
        else {
            props.dispatch(setAuthError({ error: 'Invalid login credentials.' }))
        }
    }

    return (
        <div className='container'>
            <div className='login-form'>
                <label htmlFor='input'>Email</label>
                <input id='email' type='email' name='email'></input>
                <label htmlFor='input'>Username</label>
                <input id='password' type='password' name='username'></input>
                <div className='login-form__buttons'>
                    <Button variant='outlined' color='primary' onClick={props.signUp}><Link to='/signup'>Sign Up</Link></Button>
                    <Button variant='outlined' color='primary' onClick={login}>Login</Button>
                </div>
                { props.error && <p>{ props.error }</p> }
            </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        accounts: state.accounts,
        error: state.auth.error,
    }
}

export default connect(mapStateToProps)(LoginForm);