const defaultState = {
    loggedIn: false,
    error: ``,
}

export default (state = defaultState, action) => {
    switch(action.type) {
        case 'SET_LOGGED_IN':
            return {
                ...state,
                loggedIn: action.loggedIn,
            }
        case 'SET_AUTH_ERROR':
            return {
                ...state,
                error: action.error,
            }
        default:
            return state;
    }
}