import db from '../firebase'
import { setDoc } from '@firebase/firestore';

export const addParent = ({ fName, lName, dob, email }) => {
    setDoc(db, 'parents', {
        fName,
        lName,
        dob,
        email,
    })
    .then(() => {
        // Update redux store
        'Parent added successfully'
    })
    .catch(() => 'Whoops something went wrong.');
}