import React from 'react';
import Button from '@mui/material/Button';
import Drawer from '@mui/material/Drawer';
import { BrowserRouter, Route, NavLink } from 'react-router-dom';

export default class Nav extends React.Component {
    state = {
        isOpen: false,
    }

    openNav = () => {
        this.setState(() => ({ isOpen: true }));
    }

    closeNav = () => {
        this.setState(() => ({isOpen: false}))
    }

    render() {
        return (
            <div className='header'>
                <Button onClick={this.openNav}>{this.props.title}</Button>
                <Drawer classes={{root: 'header__nav'}} anchor='left' open={this.state.isOpen} onClose={this.closeNav}>
                    <NavLink className='header__nav-link' to={'/'} exact={true}>Login</NavLink>
                    <NavLink className='header__nav-link' to={'/logs'}>Logs</NavLink>
                    <NavLink className='header__nav-link' to={'/dashboard'}>Dashboard</NavLink>
                </Drawer>
            </div>
        )
    }
}

Nav.defaultProps = {
    title: `Inkind-able`
}