const defaultState = {
    peerLogs: [],
    readingLogs: [],
    startDate: null,
    endDate: null,
}

export default (state = defaultState, action) => {
    switch(action.type) {
        case 'CREATE_NEW_LOG':
            return action.type === 'peer' ? { ...state, peerLogs: [...state.peerLogs, action.log] } : { ...state, readingLogs: [...state.readingLogs, action.log] }
        case 'SET_DATE_RANGE':
            return {
                ...state,
                startDate: action.startDate,
                endDate: action.endDate,
            }
        default:
            return state;
    }
}