import React from "react";

export default (props) => (
    <div className='log-cell'>
        <div className="log-cell__item">
            <h3 className="log-cell__day">{props.date}</h3>
        </div>
        <div className="log-cell__item log-cell__type">
            <h3 className="log-cell__type">{props.logType}</h3>
            { props.logType === `Daily Peer Cards` && <p className="log-cell__type--recommended">Recommended Card:</p> }
        </div>
        { props.Logtype === `Reading At Home` && 
        <div className="log-cell__item">
            <label htmlFor="title" className="log-cell__title">Title</label>
            <textarea name="title" rows="4" cols="50" className='log-cell__title-text'></textarea>
        </div> }
        <div className='log-cell__item'>
            <label htmlFor="minutes" className='log-cell__minutes-title'>Minutes</label>
            <input type="text" name="minutes" className='log-cell__minutes-text'></input>
        </div>
    </div>
)