import { v4 as uuidv4 } from 'uuid';

export const createNewLog = ({logType, date}) => ({
    type: 'CREATE_NEW_LOG',
    log: {
        id: uuidv4(),
        logType: ``,
        date: null,
        minutes: 0,
    }
})

export const setDateRange = ({ startDate, endDate }) => ({
    type: 'SET_DATE_RANGE',
    startDate,
    endDate,
})